
.header {
	padding-top: 11px;
	padding-bottom: 5px;
	background-color: #FFF;
	@include r(950) {
		padding-left: 15px;
		padding-right: 15px; } }

.logo {
	width: 50%;
	float: left;
	@include r(950) {
		padding-left: 15px;
		padding-right: 15px; }
	@include r(480) {
		width: 100%;
		text-align: center;
		margin-bottom: 20px; } }


.callback {
	width: 50%;
	float: right;
	position: relative;
	@include r(480) {
		width: 100%; } }

.services__item {
	width: 25%;
	height: 336px;
	float: left;
	overflow: hidden;
	position: relative;
	@include r(768) {
		width: 50%; }

	@include r(520) {
		width: 100%; } }

.production__item {
	width: 50%;
	float: left;
	padding-top: 50px;
	padding-bottom: 45px;
	position: relative;
	@include r(768) {
		width: 100%; } }


.production__choose-item {
	width: 15.50%;
	margin: 0px 2.5px;
	float: left;
	position: relative;
	@include r(480) {
		width: 24.3%;
		margin: 10px 2.5px; } }

.advantages__item {
	width: 16.66%;
	float: left;
	text-align: center;
	@include r(650) {
		width: 33.3%;
		height: 150px; }
	@include r(480) {
		width: 50%; } }

.reviews__item {
	width: 48%; }

.сall-professional__item {
	width: 52%; }

.reviews__item,
.сall-professional__item {
	float: left;
	@include r(768) {
		width: 100%; } }

.review {
	float: left;
	font-family: $arial;
	&:nth-child(odd) {
		width: 44%;
		@include r(480) {
			width: 100%; } }

	&:nth-child(even) {
		width: 46%;
		float: right;
		@include r(480) {
			width: 100%; } }

	@include r(480) {
		width: 100%; } }

.latest-news {
	width: 50%;
	float: left;
	margin: 7px 0px;
	@include r(768) {
		width: 100%;
		margin: 15px 0px; } }

.footer__logo {
	width: 21%;
	float: left;
	@include r(950) {
		width: 100%;
		text-align: center; } }

.footer__nav {
	width: 79%;
	float: left;
	@include r(950) {
		width: 100%; } }
