// **** production section **** //

.production {
	background-color: #f6bc14; }

.production__item_left {
	padding-right: 20px;
	@include r(950) {
		padding-left: 15px;
		padding-right: 15px; } }

.production__item_right {
	padding-left: 20px;
	@include r(768) {
		padding-top: 0px; } }

.production__choose {
	display: table;
	margin-top: 26px; }

.production__title {
	text-transform: uppercase;
	color: #2b2b2b;
	font-family: $roboto;
	font-weight: 700;
	margin-bottom: 23px;
	font-size: 24px;
	@include r(768) {
		text-align: center; } }

.production__text {
	color: #2b2b2b;
	font-family: $arial;
	font-size: 14px; }

.production__choose-image {
	width: 70px;
	height: 70px; }

// image collection
