.production__overlay {
	opacity: 0;
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	height: 100%;
	width: 100%;
	line-height: 72px;
	color: #252525;
	text-transform: uppercase;
	text-decoration: underline;
	font-size: 14px;
	background-color: #FFF;
	text-align: center;
	font-family: $roboto;
	font-weight: 700;
	border-radius: 3px;
	@include tr(opacity .5s);
	&:hover {
		opacity: 1;
		color: #252525; } }

.production__more {
	display: inline-block;
	background-color: #2b2b2b;
	font-size: 13px;
	color: #fff;
	padding: 6px 9px;
	text-decoration: none;
	font-family: 'Aria', sans-serif;
	border-radius: 3px;
	margin-top: 13px;
	margin-left: 1px; }

.production__choose-separator {
	background-image: url("../img/choose-separator.png");
	height: 266px;
	width: 1px;
	position: absolute;
	top: 35px;
	right: -1px;
	@include r(768) {
	    bottom: -110px;
	    top: auto;
	    right: auto;
	    left: 50%;
	    transform: rotate(90deg); } }
